body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background-color: #001a33;
    min-height: 500px;
    min-width: 400px;
    width: 95vw;
    height: 85vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  /* .board-row:after {
    clear: both;
    content: "";
    display: table;
  } */
  
  .status {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0;

  }
  
  .square {
    background: #00284d;
    border: 1px solid #999;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 80px;
    padding: 0;
    text-align: center;
    width: 78px;
    border-radius: 10px;
    border: none;
    box-shadow: 0px 5px #00111a;
    display: flex;
    align-items: center;
    justify-content: center;

  }
  .sqImg{
    width: 60%;
    height: 60%;
  }
  .board-row{
    
    width: 260px;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: auto;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  
  .game-info {
    margin-left: 20px;
  }
  .restart{
    width: 1.5rem;
    height: 1.5rem;
    background: #ccccff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 6px;
    border-radius: 8px;
    cursor: pointer;
  }
  .restart>img{
    width: 1rem;
  }
  .stat{
    height: 1.4rem;
    width: 80px;
    color: gainsboro;
    background: #00284d;
    padding: 5px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    font-family: monospace;
  }
  .stat>b{
    margin-right: 6px;
    font-weight: 900;
  }
